<template>
    <div class="top" style="background:#F2F6FC;padding:10px">
        <el-row>
            <el-col :span="12">
                <div class="grid-content bg-purple-dark" style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tabs v-model="currTab">
                            <el-tab-pane name="1" label="异常排行榜">
                                <el-table :data="homeList" @row-click="rowClick" width="100%" align="right" :stripe="true"
                                          header-align="right">
                                    <el-table-column prop="key" label="应用"></el-table-column>
                                    <el-table-column prop="value" label="异常总数"></el-table-column>
                                </el-table>
                            </el-tab-pane>
                        </el-tabs>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="12">
                <div class="grid-content bg-purple-dark" style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tabs v-model="currTab">
                            <el-tab-pane name="1" label="服务响应排行榜">
                                <el-table :data="serviceList" @row-click="rowClick" width="100%" align="right" :stripe="true"
                                          header-align="right">
                                    <el-table-column prop="key" label="应用"></el-table-column>
                                    <el-table-column prop="value" label="响应次数"></el-table-column>
                                </el-table>
                            </el-tab-pane>
                        </el-tabs>
                    </el-card>
                </div>
            </el-col>

        </el-row>

    </div>
</template>

<script>
  import service from './service'
  export default {
    name: 'home',
    data () {
      return {
        currTab: '1',
        currRow: {},
        homeList: [],
        serviceList: []
      }
    },
    methods: {
      getHomeList () {
        return service.getHomeList().then(({data = {}}) => {
          this.homeList = Object.keys(data).reduce((res, key, idx) => {
            res.push({key: key, value: data[key], index: idx + 1})
            return res
          }, [])
        })
      },
      getServiceList () {
        return service.getServiceList().then(({data = {}}) => {
          this.serviceList = Object.keys(data).reduce((res, key, idx) => {
            res.push({key: key, value: data[key], index: idx + 1})
            return res
          }, [])
        })
      },
      rowClick (row) {
        this.currRow = row
      }
    },
    computed: {},
    mounted () {
      this.getHomeList()
      this.getServiceList()
    },
    components: {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



